import React from 'react';
import Counter from './Counter'
class App extends React.Component{
  constructor(props){
    super(props);
  }

  render(){
    return(
      <div>
        <h1> Counter App </h1>
        <Counter msg="The counter value is" initalCounter={50}/>
      </div>
    );
  };
}


export default App;    


